import React, {useState, useEffect, useRef} from 'react'
import styled from 'styled-components'
import ChatInput from './ChatInput';
import axios from 'axios';
import { recieveMessageRoute, sendMessageRoute } from '../utils/APIRoutes';
import {v4 as uuidv4} from "uuid";
//import { sendImageRoute,recieveImageRoute } from '../utils/APIRoutes';

export default function ChatContainer({currentChat, socket}) {
    
   const [messages, setMessages] = useState([]);
   const [arrivalMessage,setArrivalMessage] = useState(null);
   //const [imge,setImge] = useState([]);
   const scrollRef = useRef();
   
  useEffect(() => {
    async function fetchData() {
    const data = await JSON.parse(localStorage.getItem("chat-app-user"));
    const response = await axios.post(recieveMessageRoute,{
      from: data._id,
      to: currentChat._id,
    });
    setMessages(response.data);
    console.log(response.data); 
  }
  fetchData();
  }, [currentChat]);

  // useEffect(() => {
  //   async function fetchData1() {
  //   const data = await JSON.parse(localStorage.getItem("chat-app-user"));
  //   const response = await axios.post(recieveImageRoute,{
  //     from: data._id,
  //     to: currentChat._id,
  //   });
  //   setMessages(response.data);
  //   console.log(response.data); 
  // }
  // fetchData1();
  // }, [currentChat]);

  useEffect(() => {
    async function getCurrentChat(){
      if (currentChat) {
        console.log(currentChat);
        await JSON.parse(localStorage.getItem("chat-app-user"))._id;
      }
    };
    getCurrentChat();
  }, [currentChat]);  

    const handleSendMsg = async(msg)=>{
      const data = await JSON.parse(localStorage.getItem("chat-app-user"));
      socket.current.emit("send-msg",{
        to: currentChat._id,
        from: data._id,
        msg,
      });
      await axios.post(sendMessageRoute,{
        from:data._id,
        to: currentChat._id,
        message: msg,
      });

      const msgs = [...messages];
      msgs.push({fromSelf:true, message:msg});
      setMessages(msgs);
    };

    // const handleSendImg = async(img)=>{
    //   const data = await JSON.parse(localStorage.getItem("chat-app-user"));
    //   socket.current.emit("send-img",{
    //     to: currentChat._id,
    //     from: data._id,
    //     img,
    //   });
    //   await axios.post(sendImageRoute,{
    //     from:data._id,
    //     to: currentChat._id,
    //     image: img,
    //   });

    //   const imgs = [...imge];
    //   imgs.push({fromSelf:true, image:img});
    //   setImge(imgs);
    // };

    useEffect(()=>{
      if(socket.current){
        socket.current.on("msg-recieve",(msg)=>{
          setArrivalMessage({fromSelf:false,message:msg});
        })
      }
    },[]);

    useEffect(()=>{
      arrivalMessage && setMessages((prev)=>[...prev,arrivalMessage]);
      console.log(arrivalMessage);
    },[arrivalMessage]);

    useEffect(()=>{
    scrollRef.current?.scrollIntoView({behaviour:"smooth"});
    },[messages]);

  return (
    <>
    <Container>
        <div className='chat-header'>
            <div className="user-details">
                <div className="avatar">
                <img
                      src={`data:image/svg+xml;base64,${currentChat.avatarImage}`}
                      alt=""
                    />
                </div>
                <div className="username">
                    <h3>{currentChat.username}</h3>
                </div>
            </div>
        </div>
        <div className="chat-messages">
            {
              messages.map((message)=>{
                return(
                  <div ref={scrollRef} key={uuidv4()}>
                    <div className={`message ${message.fromSelf? "sended":"recieved"}`}>
                      <div className="content">
                        <p>
                          {message.message}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })
            }
        </div>
        <ChatInput handleSendMsg={handleSendMsg}/>
    </Container>
    </>
  )
}

const Container = styled.div`
display: grid;
grid-template-rows: 10% 80% 10%;
gap: 0.1rem;
overflow: hidden;
@media screen and (min-width: 720px) and (max-width: 1080px) {
  grid-template-rows: 15% 70% 15%;
}
.chat-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3rem;
  .user-details {
    display: flex;
    align-items: center;
    margin-top:35px;
    margin-bottom:55px;
    gap: 1rem;
    .avatar {
      img {
        height: 3rem;
      }
    }
    .username {
      h3 {
        color: white;
      }
    }
  }
}
.chat-messages {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  overflow: auto;
  &::-webkit-scrollbar {
    width: 0.2rem;
    &-thumb {
      background-color: #ffffff39;
      width: 0.1rem;
      border-radius: 1rem;
    }
  }
  .message {
    display: flex;
    align-items: center;
    .content {
      max-width: 40%;
      overflow-wrap: break-word;
      padding: 1rem;
      font-size: 1.1rem;
      border-radius: 1rem;
      color: #d1d1d1;
      @media screen and (min-width: 720px) and (max-width: 1080px) {
        max-width: 70%;
      }
    }
  }
  .sended {
    justify-content: flex-end;
    .content {
      background-color: #796bc9;
    }
  }
  .recieved {
    justify-content: flex-start;
    .content {
      background-color: #254c61;
    }
  }
}

`;