import React,{useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { ToastContainer,toast } from 'react-toastify';
import loader from "../Assests/loader.gif"
import "react-toastify/dist/ReactToastify.css"
import axios from "axios";
import { SetAvatarRoute } from '../utils/APIRoutes';
import {Buffer} from "buffer";


export default function Setavatar() {

    const api = "https://api.multiavatar.com/45678945"
    const navigate = useNavigate();
    const [avatars, setAvatars] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [selectedAvatar, setSelectedAvatar] = useState(undefined);
    const toastOptions = {
      position:'bottom-right',
      autoClose:2000,
      pauseOnHover:true,
      draggable: true,
      theme: 'dark',
      };

      useEffect(() => {
        async function worked(){
        if(!localStorage.getItem("chat-app-user")){
          navigate('/login');
        } 
      }
      worked();
      },[])

      const setProfilePicture= async()=>{
         if(selectedAvatar===undefined){
          toast.error("Please select an Avatar", toastOptions);
         } else{
          const user = await JSON.parse(localStorage.getItem('chat-app-user'));
          const {data} = await axios.post(`${SetAvatarRoute}/${user._id}`,{
            image: avatars[selectedAvatar],
          });
          console.log(data);
          if(data.isSet){
            user.isAvatatarImageSet = true;
            user.avatarImage = data.image;
            localStorage.setItem('chat-app-user',JSON.stringify(user));
            navigate('/');
          }else{
            toast.error("Error saving the avatar.Please try again",toastOptions);
          }
        }
         
      };

      useEffect(() => {
        async function fetchdata(){
          const data = [];
          for(let i=0; i<4;i++){
            const image = await axios.get(`${api}/${Math.round(Math.random()*1000)}`);
            const buffer = new Buffer(image.data);
            data.push(buffer.toString("base64"));
          }
          setAvatars(data);
          setIsLoading(false);
        }
        fetchdata();
        }, []);
      
  return (
    <>
    {
      isLoading ? <Container>
        <img src={loader} alt="loader" className='loader' />
      </Container>:(
    <Container>
      <div className="title">
        <h1 style={{color:'white'}}>Pick an Avatar</h1>
      </div>
      <div className="avatars">
        {
          avatars.map((avatar,index)=>{
            return (
              <div className={`avatar ${selectedAvatar===index?"selected":""}`}>
                <img key={avatar} src={`data:image/svg+xml; base64,${avatar}`} alt="avatar" onClick={()=>setSelectedAvatar(index)}/>
              </div>
            );
          })
        }
      </div>
      <button className='submit-btn' onClick={setProfilePicture}>Set As Profile Picture</button>
      <ToastContainer/>
    </Container>
)}
    </>
  );
}

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items : center;
  flex-direction: column;
  gap: 3rem;
  background-color: #131324;
  height: 100vh;
  width : 100vw;
  .loader{
    max-inline-size:100%;
  }
  .avatars{
    display:flex;
    gap:2rem;
    .avatar{
      border:0.4rem solid transperent;
      padding:0.4rem;
      border-radius:5rem;
      justify-content:center;
      transition:0.5s cubic-bezier(0.26, -0.43, 0.77, 0.69);
      img{
          height:6rem;
      }
    }
    .selected{
      border: 0.2rem solid #4e0eff;
    }
  }
  .submit-btn{
    background-color:#997af0;
    color:white;
    padding:1rem 2rem;
    border: none;
    font-weight:bold;
    cursor: pointer;
    border-radius:0.4rem;
    font-size: 1rem;
    text-transform: uppercase;
    transition: o.5s ease-in-out;
    &:hover{
        background-color: #5c67fa;
    }
`;
